<template>
    <!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
    <uni-nav-bar dark :fixed="true" shadow background-color="var(--primary-color)" status-bar left-icon="left"
        left-text="ËøîÂõû" @clickLeft="goBack" title="AIÊô∫ËÉΩÂàÜÊûê" />

    <!-- ‰∏ªÂÜÖÂÆπÂå∫Âüü -->
    <view class="main-container">
        <!-- Âõ∫ÂÆöTabÂØºËà™ -->
        <view v-if="analysisResult" class="fixed-tabs">
            <scroll-view class="tab-scroll" scroll-x>
                <view class="tab-list">
                    <view v-for="(tab, index) in tabList" :key="index" class="tab-item"
                        :class="{ active: activeTab === tab.id }" @click="scrollToSection(tab.id)">
                        <text class="tab-emoji">{{ tab.emoji }}</text>
                        <text class="tab-text">{{ tab.name }}</text>
                    </view>
                </view>
            </scroll-view>
        </view>

        <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
        <view v-if="loading" class="loading-container">
            <view class="loading-animation">
                <view class="loading-circle">
                    <view class="loading-inner"></view>
                </view>
                <view class="loading-text">AIÊ≠£Âú®ÂàÜÊûêÊÇ®ÁöÑÂÅ•Â∫∑Êï∞ÊçÆ...</view>
                <view class="loading-tips">
                    <text>Ê≠£Âú®ÂàÜÊûêËøë{{ analysisData.Days }}Â§©ÁöÑÊï∞ÊçÆ</text>
                </view>
            </view>
        </view>

        <!-- ÂàÜÊûêÁªìÊûúÂ±ïÁ§∫ -->
        <view v-else-if="analysisResult" class="result-container">
            <!-- ÂÅ•Â∫∑ÊÄªËØÑ -->
            <view id="overview" class="health-overview section">
                <view class="card-header">
                    <text class="card-title">üéØ ÂÅ•Â∫∑ÊÄªËØÑ</text>
                </view>
                <view class="health-score-section">
                    <view class="score-circle">
                        <view class="score-number">{{ analysisResult.OverallHealthScore }}</view>
                        <view class="score-total">/100</view>
                    </view>
                    <view class="health-level">
                        <text class="level-text" :class="getHealthLevelClass()">{{ analysisResult.HealthLevel }}</text>
                        <text class="level-desc">ÂÅ•Â∫∑Á≠âÁ∫ß</text>
                    </view>
                </view>
                <view class="summary-text">{{ analysisResult.Summary }}</view>
            </view>

            <!-- ÂÅ•Â∫∑È£éÈô©ËØÑ‰º∞ -->
            <view id="risks" class="risk-section section">
                <view class="card-header">
                    <text class="card-title">‚ö†Ô∏è ÂÅ•Â∫∑È£éÈô©ËØÑ‰º∞</text>
                </view>
                <view class="risk-list">
                    <view v-for="(risk, index) in analysisResult.HealthRisks" :key="index" class="risk-item">
                        <view class="risk-header">
                            <view class="risk-info">
                                <text class="risk-emoji">{{ getRiskEmoji(risk.RiskType) }}</text>
                                <text class="risk-type">{{ risk.RiskType }}</text>
                            </view>
                            <uni-tag :text="risk.RiskLevel" :type="getRiskLevelType(risk.RiskLevel)" size="mini" />
                        </view>
                        <view class="risk-description">{{ risk.Description }}</view>
                        <view class="risk-suggestions">
                            <text class="suggestions-label">üí° Âª∫ËÆÆ:</text>
                            <text class="suggestions-text">{{ risk.Suggestions }}</text>
                        </view>
                    </view>
                </view>
            </view>

            <!-- Ëê•ÂÖªÂàÜÊûê -->
            <view id="nutrition" class="nutrition-section section">
                <view class="card-header">
                    <text class="card-title">ü•ó Ëê•ÂÖªÂàÜÊûê</text>
                </view>
                <view class="nutrition-score">
                    <text class="score-label">üìä Ëê•ÂÖªÂùáË°°ËØÑÂàÜ</text>
                    <view class="score-bar">
                        <view class="score-progress"
                            :style="{ width: analysisResult.NutritionAnalysis.NutritionBalanceScore + '%' }"></view>
                    </view>
                    <text class="score-value">{{ analysisResult.NutritionAnalysis.NutritionBalanceScore }}/100</text>
                </view>

                <view class="nutrition-assessments">
                    <view class="assessment-item">
                        <text class="assessment-label">üî• ÁÉ≠ÈáèÊëÑÂÖ•ËØÑ‰º∞</text>
                        <text class="assessment-value">{{ analysisResult.NutritionAnalysis.CalorieIntakeAssessment }}</text>
                    </view>
                    <view class="assessment-item">
                        <text class="assessment-label">ü•© ËõãÁôΩË¥®ËØÑ‰º∞</text>
                        <text class="assessment-value">{{ analysisResult.NutritionAnalysis.ProteinAssessment }}</text>
                    </view>
                    <view class="assessment-item">
                        <text class="assessment-label">üçû Á¢≥Ê∞¥ÂåñÂêàÁâ©ËØÑ‰º∞</text>
                        <text class="assessment-value">{{ analysisResult.NutritionAnalysis.CarbohydrateAssessment }}</text>
                    </view>
                    <view class="assessment-item">
                        <text class="assessment-label">ü•ë ËÑÇËÇ™ËØÑ‰º∞</text>
                        <text class="assessment-value">{{ analysisResult.NutritionAnalysis.FatAssessment }}</text>
                    </view>
                </view>

                <view class="dietary-recommendations">
                    <text class="recommendations-title">üçΩÔ∏è È•ÆÈ£üÂª∫ËÆÆ</text>
                    <view v-for="(recommendation, index) in analysisResult.NutritionAnalysis.DietaryRecommendations"
                        :key="index" class="recommendation-item">
                        <text class="recommendation-text">{{ index + 1 }}. {{ recommendation }}</text>
                    </view>
                </view>
            </view>

            <!-- ËøêÂä®ÂàÜÊûê -->
            <view id="sport" class="sport-section section">
                <view class="card-header">
                    <text class="card-title">üèÉ‚Äç‚ôÇÔ∏è ËøêÂä®ÂàÜÊûê</text>
                </view>
                <view class="sport-score">
                    <text class="score-label">üìà ËøêÂä®È¢ëÁéáËØÑÂàÜ</text>
                    <view class="score-bar">
                        <view class="score-progress"
                            :style="{ width: analysisResult.SportAnalysis.ExerciseFrequencyScore + '%' }"></view>
                    </view>
                    <text class="score-value">{{ analysisResult.SportAnalysis.ExerciseFrequencyScore }}/100</text>
                </view>

                <view class="sport-assessments">
                    <view class="assessment-item">
                        <text class="assessment-label">üí™ ËøêÂä®ÈáèËØÑ‰º∞</text>
                        <text class="assessment-value">{{ analysisResult.SportAnalysis.ExerciseVolumeAssessment }}</text>
                    </view>
                    <view class="assessment-item">
                        <text class="assessment-label">üî• ÁÉ≠ÈáèÊ∂àËÄóËØÑ‰º∞</text>
                        <text class="assessment-value">{{ analysisResult.SportAnalysis.CaloriesBurnedAssessment }}</text>
                    </view>
                    <view class="assessment-item">
                        <text class="assessment-label">üéØ ËøêÂä®Â§öÊ†∑ÊÄßËØÑ‰º∞</text>
                        <text class="assessment-value">{{ analysisResult.SportAnalysis.ExerciseVarietyAssessment }}</text>
                    </view>
                </view>

                <view class="exercise-recommendations">
                    <text class="recommendations-title">üèãÔ∏è‚Äç‚ôÄÔ∏è ËøêÂä®Âª∫ËÆÆ</text>
                    <view v-for="(recommendation, index) in analysisResult.SportAnalysis.ExerciseRecommendations"
                        :key="index" class="recommendation-item">
                        <text class="recommendation-text">{{ index + 1 }}. {{ recommendation }}</text>
                    </view>
                </view>
            </view>

            <!-- ÊåáÊ†áÂàÜÊûê -->
            <view id="indicators" class="indicators-section section">
                <view class="card-header">
                    <text class="card-title">üìä ÊåáÊ†áÂàÜÊûê</text>
                </view>
                <view class="indicators-list">
                    <view v-for="(indicator, index) in analysisResult.IndicatorAnalyses" :key="index"
                        class="indicator-item">
                        <view class="indicator-header">
                            <view class="indicator-info">
                                <text class="indicator-emoji">{{ getIndicatorEmoji(indicator.IndicatorName) }}</text>
                                <text class="indicator-name">{{ indicator.IndicatorName }}</text>
                            </view>
                            <uni-tag :text="indicator.Status" :type="getIndicatorStatusType(indicator.Status)"
                                size="mini" />
                        </view>
                        <view class="indicator-details">
                            <view class="detail-row">
                                <text class="detail-label">üìè ÂΩìÂâçÂÄº:</text>
                                <text class="detail-value">{{ indicator.CurrentValue }}</text>
                            </view>
                            <view class="detail-row">
                                <text class="detail-label">üìã Ê≠£Â∏∏ËåÉÂõ¥:</text>
                                <text class="detail-value">{{ indicator.NormalRange }}</text>
                            </view>
                            <view class="detail-row">
                                <text class="detail-label">üìà Ë∂ãÂäø:</text>
                                <text class="detail-value">{{ indicator.Trend }}</text>
                            </view>
                            <view class="indicator-advice">
                                <text class="advice-label">üí° Âª∫ËÆÆ:</text>
                                <text class="advice-text">{{ indicator.Advice }}</text>
                            </view>
                        </view>
                    </view>
                </view>
            </view>

            <!-- ÂÅ•Â∫∑Âª∫ËÆÆ -->
            <view id="recommendations" class="recommendations-section section">
                <view class="card-header">
                    <text class="card-title">üí° ÂÅ•Â∫∑Âª∫ËÆÆ</text>
                </view>
                <view class="recommendations-list">
                    <view v-for="(recommendation, index) in analysisResult.Recommendations" :key="index"
                        class="recommendation-card">
                        <view class="recommendation-header">
                            <view class="recommendation-info">
                                <text class="recommendation-emoji">{{
                                    getRecommendationEmoji(recommendation.RecommendationType) }}</text>
                                <text class="recommendation-title">{{ recommendation.Title }}</text>
                            </view>
                            <uni-tag :text="recommendation.Priority" :type="getPriorityType(recommendation.Priority)"
                                size="mini" />
                        </view>
                        <view class="recommendation-type">
                            <text class="type-label">üè∑Ô∏è Á±ªÂûã:</text>
                            <text class="type-value">{{ recommendation.RecommendationType }}</text>
                        </view>
                        <view class="recommendation-content">{{ recommendation.Content }}</view>
                        <view class="recommendation-effect">
                            <text class="effect-label">‚ú® È¢ÑÊúüÊïàÊûú:</text>
                            <text class="effect-text">{{ recommendation.ExpectedEffect }}</text>
                        </view>
                    </view>
                </view>
            </view>

            <!-- ÂàÜÊûêÊó∂Èó¥ -->
            <view class="analysis-time">
                <text class="time-text">ÂàÜÊûêÊó∂Èó¥: {{ formatAnalysisTime(Data.AnalysisTime) }}</text>
            </view>
        </view>

        <!-- ÈîôËØØÁä∂ÊÄÅ -->
        <view v-else-if="error" class="error-container">
            <view class="error-content">
                <uni-icons type="info" size="60" color="#ff6b6b"></uni-icons>
                <text class="error-text">ÂàÜÊûêÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï</text>
                <button class="retry-button" @click="getAiAnalyseApi" type="primary" size="mini">ÈáçÊñ∞ÂàÜÊûê</button>
            </view>
        </view>
    </view>
</template>

<script setup>
import { useCommonStore } from '@/store';
import { Post } from "@/utils/http";
import { onLoad, onReady, onShow, onPageScroll } from "@dcloudio/uni-app";
import { computed, nextTick, reactive, ref } from 'vue';

// Ëé∑Âèñstore
const commonStore = useCommonStore();
const Token = computed(() => commonStore.Token)
const UserInfo = computed(() => commonStore.UserInfo)
const RoleType = computed(() => commonStore.RoleType)
const UserId = computed(() => commonStore.UserId)

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const loading = ref(false);
const error = ref(false);
const Data = ref(null);
const analysisResult = ref(null);
const activeTab = ref('overview');
const scrollThrottleTimer = ref(null); // Ê∑ªÂä†ÊªöÂä®ËäÇÊµÅÂÆöÊó∂Âô®
const sectionPositions = ref([]); // ÁºìÂ≠òsection‰ΩçÁΩÆ‰ø°ÊÅØ
const recalcTimer = ref(null); // ÈáçÊñ∞ËÆ°ÁÆó‰ΩçÁΩÆÁöÑÈò≤ÊäñÂÆöÊó∂Âô®

// ÂàÜÊûêËØ∑Ê±ÇÂèÇÊï∞
const analysisData = reactive({
    UserId: '',
    Days: 7
});

// TabÂàóË°®ÈÖçÁΩÆ
const tabList = ref([
    { id: 'overview', name: 'ÊÄªËØÑ', emoji: 'üéØ' },
    { id: 'risks', name: 'È£éÈô©', emoji: '‚ö†Ô∏è' },
    { id: 'nutrition', name: 'Ëê•ÂÖª', emoji: 'ü•ó' },
    { id: 'sport', name: 'ËøêÂä®', emoji: 'üèÉ‚Äç‚ôÇÔ∏è' },
    { id: 'indicators', name: 'ÊåáÊ†á', emoji: 'üìä' },
    { id: 'recommendations', name: 'Âª∫ËÆÆ', emoji: 'üí°' }
]);

// ÁîüÂëΩÂë®ÊúüÈí©Â≠ê
onLoad(async (option) => {
    analysisData.UserId = UserId.value;
    // È°µÈù¢ÊòæÁ§∫Êó∂ÁöÑÈÄªËæë
    getAiAnalyseApi();
});

// Èò≤ÊäñÈáçÊñ∞ËÆ°ÁÆósection‰ΩçÁΩÆ
const debouncedRecalculate = () => {
    if (recalcTimer.value) {
        clearTimeout(recalcTimer.value);
    }

    recalcTimer.value = setTimeout(() => {
        if (analysisResult.value) {
            calculateSectionPositions();
        }
    }, 300);
};

// ÁõëÂê¨È°µÈù¢Â∞∫ÂØ∏ÂèòÂåñ
onShow(async () => {


    // ÁõëÂê¨È°µÈù¢Â∞∫ÂØ∏ÂèòÂåñ‰∫ã‰ª∂
    uni.onWindowResize(() => {
        debouncedRecalculate();
    });
});

// ÁõëÂê¨È°µÈù¢ÊªöÂä® - Ê∑ªÂä†ËäÇÊµÅ‰ºòÂåñ
onPageScroll((e) => {
    if (!analysisResult.value) return;

    // ÊªöÂä®ËäÇÊµÅ‰ºòÂåñÔºåÈÅøÂÖçÈ¢ëÁπÅËÆ°ÁÆó
    if (scrollThrottleTimer.value) {
        clearTimeout(scrollThrottleTimer.value);
    }

    scrollThrottleTimer.value = setTimeout(() => {
        updateActiveTab(e.scrollTop);
    }, 50); // 50ms ËäÇÊµÅ
});

onReady(async () => {
    // È°µÈù¢ÂáÜÂ§áÂ∞±Áª™ÂêéÔºåÂ¶ÇÊûúÊúâÊï∞ÊçÆÂàôËÆ°ÁÆó‰ΩçÁΩÆ
    if (analysisResult.value) {
        await nextTick();
        setTimeout(() => {
            calculateSectionPositions();
        }, 200);
    }
});

// APIË∞ÉÁî®ÊñπÊ≥ï
const getAiAnalyseApi = async () => {
    try {
        loading.value = true;
        error.value = false;
        analysisResult.value = null;

        let response = await Post('/AiAnalyse/AnalyzeUserHealth', {
            UserId: UserId.value,
            Days: 7
        });

        Data.value = response.Data;
        analysisResult.value = response.Data.AnalysisResult;

        // Êï∞ÊçÆÂä†ËΩΩÂÆåÊàêÂêéËÆ°ÁÆósection‰ΩçÁΩÆ
        await nextTick();
        setTimeout(() => {
            calculateSectionPositions();
        }, 100); // ÁªôDOM‰∏ÄÁÇπÊó∂Èó¥ÂÆåÊàêÊ∏≤Êüì

    } catch (err) {
        error.value = true;
        console.error('AIÂàÜÊûêÂ§±Ë¥•:', err);
        uni.showToast({
            title: 'ÂàÜÊûêÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
            icon: 'error'
        });
    } finally {
        loading.value = false;
    }
};

// Â∑•ÂÖ∑ÊñπÊ≥ï
const goBack = () => {
    uni.navigateBack();
};

// Ëé∑ÂèñÂÅ•Â∫∑Á≠âÁ∫ßÊ†∑ÂºèÁ±ª
const getHealthLevelClass = () => {
    const level = analysisResult.value.HealthLevel;
    if (level === '‰ºòÁßÄ') return 'level-excellent';
    if (level === 'ËâØÂ•Ω') return 'level-good';
    if (level === '‰∏ÄËà¨') return 'level-average';
    return 'level-poor';
};

// Ëé∑ÂèñÈ£éÈô©Á≠âÁ∫ßÊ†áÁ≠æÁ±ªÂûã
const getRiskLevelType = (level) => {
    if (level === 'È´ò') return 'error';
    if (level === '‰∏≠') return 'warning';
    return 'success';
};

// Ëé∑ÂèñÊåáÊ†áÁä∂ÊÄÅÊ†áÁ≠æÁ±ªÂûã
const getIndicatorStatusType = (status) => {
    if (status === 'Ê≠£Â∏∏') return 'success';
    if (status === 'ÂÅèÈ´ò' || status === 'ÂÅè‰Ωé') return 'warning';
    return 'error';
};

// Ëé∑Âèñ‰ºòÂÖàÁ∫ßÊ†áÁ≠æÁ±ªÂûã
const getPriorityType = (priority) => {
    if (priority === 'È´ò') return 'error';
    if (priority === '‰∏≠') return 'warning';
    return 'primary';
};

// Ê†ºÂºèÂåñÂàÜÊûêÊó∂Èó¥
const formatAnalysisTime = (timeString) => {
    const date = new Date(timeString);
    return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
    });
};

// Ëé∑ÂèñÈ£éÈô©Á±ªÂûãÂØπÂ∫îÁöÑemoji
const getRiskEmoji = (riskType) => {
    if (riskType.includes('‰ΩìÈáç')) return '‚öñÔ∏è';
    if (riskType.includes('Ë°ÄÁ≥ñ')) return 'ü©∏';
    if (riskType.includes('Ëê•ÂÖª')) return 'ü•ó';
    if (riskType.includes('ËøêÂä®')) return 'üèÉ‚Äç‚ôÇÔ∏è';
    if (riskType.includes('ÂøÉË°ÄÁÆ°')) return '‚ù§Ô∏è';
    return '‚ö†Ô∏è';
};

// Ëé∑ÂèñÊåáÊ†áÂêçÁß∞ÂØπÂ∫îÁöÑemoji
const getIndicatorEmoji = (indicatorName) => {
    if (indicatorName.includes('‰ΩìÈáç')) return '‚öñÔ∏è';
    if (indicatorName.includes('Ë°ÄÁ≥ñ')) return 'ü©∏';
    if (indicatorName.includes('Ë°ÄÊ∞ß')) return 'ü©∏';
    if (indicatorName.includes('ËÇ∫Ê¥ªÈáè')) return 'üí®';
    if (indicatorName.includes('‰ΩìÊ∏©')) return 'üå°Ô∏è';
    if (indicatorName.includes('Áî≤Áä∂ËÖ∫')) return 'ü¶ã';
    return 'üìä';
};

// Ëé∑ÂèñÂª∫ËÆÆÁ±ªÂûãÂØπÂ∫îÁöÑemoji
const getRecommendationEmoji = (recommendationType) => {
    if (recommendationType === 'ÂåªÁñó') return 'üè•';
    if (recommendationType === 'È•ÆÈ£ü') return 'üçΩÔ∏è';
    if (recommendationType === 'ËøêÂä®') return 'üèÉ‚Äç‚ôÇÔ∏è';
    if (recommendationType === 'ÁîüÊ¥ª‰π†ÊÉØ') return 'üè°';
    return 'üí°';
};

// ÊªöÂä®Âà∞ÊåáÂÆöÂå∫Âüü
const scrollToSection = async (sectionId) => {
    activeTab.value = sectionId;

    // ‰ΩøÁî® uni.createSelectorQuery Ëé∑ÂèñÁ≤æÁ°Æ‰ΩçÁΩÆ
    await nextTick();

    const query = uni.createSelectorQuery();
    query.select(`#${sectionId}`).boundingClientRect();
    query.selectViewport().scrollOffset();
    query.exec((res) => {
        if (res[0]) {
            // ËÆ°ÁÆóÁõÆÊ†áÊªöÂä®‰ΩçÁΩÆÔºåÂáèÂéªÂõ∫ÂÆötabÁöÑÈ´òÂ∫¶ÂÅèÁßª
            let targetScrollTop;
            // #ifdef MP
            targetScrollTop = res[0].top + res[1].scrollTop - 180; // Â∞èÁ®ãÂ∫èÁéØÂ¢ÉÂÅèÁßªÈáè
            // #endif
            // #ifndef MP
            targetScrollTop = res[0].top + res[1].scrollTop - 100; // ÈùûÂ∞èÁ®ãÂ∫èÁéØÂ¢ÉÂÅèÁßªÈáè
            // #endif

            uni.pageScrollTo({
                scrollTop: Math.max(0, targetScrollTop), // Á°Æ‰øù‰∏ç‰ºöÊªöÂä®Âà∞Ë¥üÂÄº
                duration: 300
            });
        }
    });
};

// ËÆ°ÁÆóÂπ∂ÁºìÂ≠òÊâÄÊúâsectionÁöÑ‰ΩçÁΩÆ‰ø°ÊÅØ
const calculateSectionPositions = async () => {
    if (!analysisResult.value) return;

    await nextTick();

    const sectionIds = ['overview', 'risks', 'nutrition', 'sport', 'indicators', 'recommendations'];
    const positions = [];

    return new Promise((resolve) => {
        let completed = 0;

        sectionIds.forEach((sectionId, index) => {
            const query = uni.createSelectorQuery();
            query.select(`#${sectionId}`).boundingClientRect();
            query.selectViewport().scrollOffset();
            query.exec((res) => {
                if (res[0] && res[1]) {
                    positions[index] = {
                        id: sectionId,
                        top: res[0].top + res[1].scrollTop,
                        bottom: res[0].top + res[1].scrollTop + res[0].height
                    };
                }

                completed++;
                if (completed === sectionIds.length) {
                    // Êåâtop‰ΩçÁΩÆÊéíÂ∫è
                    sectionPositions.value = positions.filter(p => p).sort((a, b) => a.top - b.top);
                    resolve();
                }
            });
        });
    });
};

// Êõ¥Êñ∞Ê¥ªË∑Étab - Âü∫‰∫éÁºìÂ≠òÁöÑ‰ΩçÁΩÆ‰ø°ÊÅØËøõË°åÈ´òÊïàËÆ°ÁÆó
const updateActiveTab = (scrollTop) => {
    if (!analysisResult.value || sectionPositions.value.length === 0) return;

    let offsetTop;
    // #ifdef MP
    offsetTop = 280; // Â∞èÁ®ãÂ∫èÁéØÂ¢ÉÔºöË∞ÉÊï¥tabÂØºËà™È´òÂ∫¶ + ÁºìÂÜ≤
    // #endif
    // #ifndef MP
    offsetTop = 200; // ÈùûÂ∞èÁ®ãÂ∫èÁéØÂ¢ÉÔºöÂéüÂßãtabÂØºËà™È´òÂ∫¶ + ÁºìÂÜ≤
    // #endif
    const adjustedScrollTop = scrollTop + offsetTop;

    // ‰ªéÂêéÂæÄÂâçÈÅçÂéÜÔºåÊâæÂà∞Á¨¨‰∏Ä‰∏™topÂ∞è‰∫éÁ≠â‰∫éÂΩìÂâçÊªöÂä®‰ΩçÁΩÆÁöÑsection
    for (let i = sectionPositions.value.length - 1; i >= 0; i--) {
        const section = sectionPositions.value[i];
        if (adjustedScrollTop >= section.top) {
            if (activeTab.value !== section.id) {
                activeTab.value = section.id;
            }
            break;
        }
    }
};
